import React, {useCallback, useRef, useState} from 'react';
import {DndProvider, useDrag, useDrop} from 'react-dnd';
import styled from "styled-components";
import update from 'immutability-helper'
import './App.css';
import Backend from 'react-dnd-html5-backend'
import Clock from './components/clock/clock.component';
import PortfolioBlock from './components/Projects-block/Projects-block.component';
import News from './components/feed-block/Feed-block.component'
import Backgroud from './components/background/background.component'
const ItemTypes = {
	CARD: 'card',
};


///TODO write a tests
///https://airbnb.io/enzyme/

function App() {
	return (
		<div className="App">
			<DndProvider backend={Backend}>
				<Container/>
			</DndProvider>
		<Backgroud/>
		</div>
	)
}

const Item = styled.div`
	opacity: ${props=>props.opacity};
	height:max-content;
	transition: .3s all ease;
	width:max-content;
	padding: 0.5rem 1rem;
	margin-bottom: .5rem;
	cursor: grab;
`
const CardHOC = ({id, Text, index, moveCard}) => {
	const ref = useRef(null);
	const [, drop] = useDrop({
		accept: ItemTypes.CARD,
		hover(item, monitor) {
			if (!ref.current) {
				return
			}
			const dragIndex = item.index;
			const hoverIndex = index;
			if (dragIndex === hoverIndex) {
				return
			}
			const hoverBoundingRect = ref.current.getBoundingClientRect();
			const hoverMiddleY =
				(hoverBoundingRect.bottom - hoverBoundingRect.top) / 2
			const clientOffset = monitor.getClientOffset()
			const hoverClientY = clientOffset.y - hoverBoundingRect.top
			
			if (dragIndex < hoverIndex && hoverClientY < hoverMiddleY) {
				return
			}
			if (dragIndex > hoverIndex && hoverClientY > hoverMiddleY) {
				return
			}
			moveCard(dragIndex, hoverIndex)
			item.index = hoverIndex
		},
	})
	const [{isDragging}, drag] = useDrag({
		item: {type: ItemTypes.CARD, id, index},
		collect: monitor => ({
			isDragging: monitor.isDragging(),
		}),
	})
	const opacity = isDragging ? 0 : 1;
	drag(drop(ref));
	return (
		<Item ref={ref} opacity={opacity}>
			{Text}
		</Item>
	)
}


const Container = () => {
	
	const [cards, setCards] = useState([
		{
			id: 1,
			data: <Clock/>,
		},
		{
			id: 2,
			data: <News/>,
		},
		{
			id: 3,
			data: <PortfolioBlock/>,
		},
	])
	const moveCard = useCallback(
		(dragIndex, hoverIndex) => {
			const dragCard = cards[dragIndex]
			setCards(
				update(cards, {
					$splice: [
						[dragIndex, 1],
						[hoverIndex, 0, dragCard],
					],
				}),
			)
		},
		[cards],
	)
	const renderCard = (card, index) => {
		return (
			<CardHOC
				key={card.id}
				index={index}
				id={card.id}
				Text={card.data}
				moveCard={moveCard}
			/>
		)
	}
	return (
		<>
			<div style={{
				display: "flex",
			}}>{cards.map((card, i) => renderCard(card, i))}</div>
		</>
	)
	
};




export default App;
